import PropTypes from 'prop-types';
import styles from './transactionhistory.module.css';



const TransactionHistory = ({items}) => {
    return (
        <table className={styles.transaction_history}>
            <thead>
                <tr>
                    <th className={styles.tr_item}>Type</th>
                    <th className={styles.tr_item}>Amount</th>
                    <th className={styles.tr_item}>Currency</th>
                </tr>
            </thead>

            <tbody>
                {items.map(({ id, type, amount, currency }) =>
                    <tr key={id} className={styles.tr_item}>
                        <td className={styles.tr_item} >{type}</td>
                        <td className={styles.tr_item}>{amount}</td>
                        <td className={styles.tr_item}>{currency}</td>
                    </tr>
                )}
            </tbody>
        </table>
    );
};

TransactionHistory.propTypes = {
    id: PropTypes.string,
    type: PropTypes.string,
    tag: PropTypes.string,
    currency: PropTypes.number,
   
};

export default TransactionHistory;